.App_Header{
    display: flex;
    background-color: #fff;
    padding: 5px;
}
.Header__Menu{
    margin: 0 auto;
}
.Header__Menu > ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    align-items: center;
}
.Header__Menu > ul > li > a{
    text-decoration: none;
    margin: 10px;
    font-weight: 100;
    font-size: 26px;
    color: #045960;
    transition: .2s;
}
.Header__Menu > ul > li > a:hover{
    color: #024046;
}